<template>
	<div id="store-qualification">
		<public-header title="营业资质"></public-header>
		<div class="content">
			<van-cell-group class="border-0px">
				<van-cell title="营业执照" />
				<div class="uploader-wrapper flex-lr">
					<uploader max="1" :value="(qualification.business[0] && qualification.business[0].url) ? qualification.business : []" @delete="onDeleteBusiness"  @onGetUrl="onSetBusiness">
						<div slot="img" class="upload-bg">
							<i class="icon icon-add"></i>
							请上传营业执照照片
						</div>
					</uploader>
				</div>
				<van-cell title="餐饮服务许可证" />
				<div class="uploader-wrapper flex-lr">
					<uploader max="1" :value="(qualification.service[0] && qualification.service[0].url) ? qualification.service : []" @delete="onDeleteService" @onGetUrl="onSetService">
						<div slot="img" class="upload-bg">
							<i class="icon icon-add"></i>
							请上传餐饮服务许可证照片
						</div>
					</uploader>
				</div>
				<van-cell title="更多资质" />
				<div class="uploader-wrapper flex-lr">
					<uploader max="1" :value="(qualification.more1[0] && qualification.more1[0].url) ? qualification.more1 : []" @delete="onDeleteMore1" @onGetUrl="onSetMore1">
						<div slot="img" class="upload-bg">
							<i class="icon icon-add"></i>
							请上传更多资质一照片
						</div>
					</uploader>
					<uploader max="1" :value="(qualification.more2[0] && qualification.more2[0].url) ? qualification.more2 : []" @delete="onDeleteMore2" @onGetUrl="onSetMore2">
						<div slot="img" class="upload-bg">
							<i class="icon icon-add"></i>
							请上传更多资质二照片
						</div>
					</uploader>
				</div>
			</van-cell-group>
			<div class="tip font-14 c-gray padding-15-lr padding-10-tb">建议上传图片前添加水印</div>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit" class="font-16">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import uploader from "@/components/uploader"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				id: 0,
				type: 'qualification',
				store: {
					qualification: {
						business: {},
						service: {},
						more1: {},
						more2: {}
					}
				},
				qualification: {
					business: [{url: ''}],
					service: [{url: ''}],
					more1: [{url: ''}],
					more2: [{url: ''}],
				},
				islegal: false,
				showPreLoading: true,

			}
		},
		components: {
			publicHeader,
			uploader
		},
		methods: {
			onSetImg(type, value) {
				this.qualification[type][0].url = value[0].url;
				this.store.qualification[type]['thumb'] = value[0].filename;
			},
			onSetBusiness(value) {
				this.onSetImg('business', value);
			},
			onSetService(value) {
				this.onSetImg('service', value);
			},
			onSetMore1(value) {
				this.onSetImg('more1', value);
			},
			onSetMore2(value) {
				this.onSetImg('more2', value);
			},

			onDelete(type, value) {
				this.qualification[type].push({url: ''});
				this.store.qualification[type]['thumb'] = '';
 			},
			onDeleteBusiness(value) {
				this.onDelete('business', value);
			},
			onDeleteService(value) {
				this.onDelete('service', value);
			},
			onDeleteMore1(value) {
				this.onDelete('more1', value);
			},
			onDeleteMore2(value) {
				this.onDelete('more2', value);
			},

			onLoad() {
				var that = this;
				getData({
					url: 'plateform/store/shop/setting',
					vue: that,
					autoAssign: true,
					variable: 'store',
					data: {
						id: that.id,
						type: that.type
					},
					success: function(res) {
						that.islegal = true;
						for(var x in that.store.qualification) {
							if(x == 'business' || x == 'service' || x == 'more1' || x == 'more2') {
								that.qualification[x][0]['url'] = that.store.qualification[x]['thumb'];
							}
						}
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				setData({
					url: 'plateform/store/shop/setting',
					vue: that,
					data: {
						id: that.id,
						type: that.type,
						value: JSON.stringify(that.store.qualification),
					},
					redirect: that.util.getUrl({path: 'pages/merchant/store/index', query: {id: that.id}}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-qualification .content{
		background-color: #fff;
		padding-bottom: 15px;
	}
	#store-qualification .uploader-wrapper .uploader{
		width: 50%;
	}
	#store-qualification .uploader-wrapper .uploader:first-child{
		padding: 10px 7.5px 0 15px;
	}
	#store-qualification .uploader-wrapper .uploader:nth-child(2){
		padding: 10px 15px 0 7.5px;
	}
	#store-qualification .uploader-wrapper .uploader .van-col--6{
		width: 100%;
		height: 100px;
	}
	#store-qualification .uploader-wrapper .upload-bg{
		width: 100%;
		height: 100%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		background-color: #4FAE52;
		border-radius: 3px;
		color: #fff;
		opacity: 0.5;
		font-size: 13px;
	}
	#store-qualification .uploader-wrapper .upload-bg .icon-add{
		font-size: 32px;
		border: 1px solid #fff;
		border-radius: 100px;
		color: #4FAE52;
		background-color: #fff;
		margin-bottom: 10px;
	}
</style>


